section{
    margin-bottom: -273px;
}

#sprayContainer{
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 950px;
    left: 440px;
    top: -250px;
}

.spray{
    height: auto;
    width: 477px;
    position: relative;
    top: 198px;
    left: -40px;
    transform: rotate(1deg) scaleX(-1);
}


.container1, .container2, .container3, .container4,
.container5,.container6, .container7,.container8 {
    display: flex;
    height: 267px;
    width: 202px;
    min-width: 160px;
    box-sizing: border-box;
    object-fit: fill;
    margin: 15px 15px 5px 15px;
    
    position: relative;
    border-radius: 8px;
}

.container1 img, .container2 img, .container3 img, .container4 img, 
.container5 img,.container6 img, .container7 img, .container8 img {
    display: flex;
    overflow: auto;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    position: relative;
    border: 1px solid black;
}

.content1, .content2, .content3, .content4, 
.content5, .content6, .content7, .content8 {
    overflow: auto;
    font-family: liebelotte, sans-serif;
    font-weight: 700;
    font-style: normal;
    border-radius: 0px 0px 8px 8px;
    font-size: 34px;
    letter-spacing: 2px;
    position: absolute;
    width: 100%;
    bottom: 0;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.50);
    color: white;
    text-align: center;
}

.container1:hover, .container2:hover, .container3:hover, .container4:hover, 
.container5:hover, .container6:hover, .container7:hover, .container8:hover {
    transform:scale(1.15);
    box-shadow: 0 0 18px rgba(0, 0, 0, 2.75);
}

.Group2{
    position: relative;
    display: flex;
    /* flex-direction: row; */
    flex-wrap: wrap;
    justify-content: space-evenly;
}

@media (max-width: 1024px) {
    #sprayContainer{
        width: 700px;
        left: 300px;
        top: -186px;
        justify-content: center;
    }
    
    .spray{
        width: 350px;
    }

    .container1, .container2, .container3, .container4,
    .container5,.container6, .container7,.container8{
        width: 180px;
        height: 238px;
        
    }

    .container7,.container8{
        margin-bottom: 56px;
    }

    .content1, .content2, .content3, .content4, 
    .content5, .content6, .content7, .content8 {
        font-size: 30px;
    }
}

@media (max-width: 768px) {
    #sprayContainer{
        width: 528px;
        left: 228px;
        top: -179px;
        justify-content: center;
    }

    .spray{
        width: 310px;
        left: -68px;
    }

    .container1, .container2, .container3, .container4,
    .container5,.container6, .container7,.container8{
        width: 140px;
        height: 200px;
        min-width: 130px;
        
    }

    .content1, .content2, .content3, .content4, 
    .content5, .content6, .content7, .content8 {
        font-size: 29px;
    }
}

@media (max-width: 427px) {
    .spray{
        width: 306px;
        position: relative;
        top: -43px;
        left: -137px;
        transform: rotate(37deg) scaleX(-1);
    }

    #sprayContainer{
        width: 367px;
        left: 30px;
        top: -137px;
        justify-content: center;
        
    }

    .container1, .container2, .container3, .container4,
    .container5,.container6, .container7,.container8{
        width: 130px;
        height: 190px;
        min-width: 120px;
    }

    .container7,.container8{
        margin-bottom: 146px;
    }
}

@media (max-width: 375px) {
    .spray{
        top: -27px;
        left: -141px;
        width: 291px;
        transform: rotate(44deg) scaleX(-1);
    }

    #sprayContainer{
        width: 366px;
        left: 0px;
        top: -120px;
    }

    .container1, .container2, .container3, .container4,
    .container5,.container6, .container7,.container8{
        width: 120px;
        height: 170px;
        min-width: 120px;
    }

    .content1, .content2, .content3, .content4, 
    .content5, .content6, .content7, .content8 {
        font-size: 27px;
    }
}

